import "bootstrap-datepicker/dist/css/bootstrap-datepicker3.min.css!";
import jquery = require("jquery");
import "bootstrap-datepicker";
import "bootstrap-datepicker/dist/locales/bootstrap-datepicker.pt-BR.min";
import "bootstrap-datepicker/dist/locales/bootstrap-datepicker.es.min";
import * as moment from "moment";
import {Validations} from "./../core/validations";
import {AInput} from "./abstract/a-input";
import {InputAddon} from "./input-addon";

export enum EDatePickerStartType{
	month,year,decade
}

export interface IConfigDatePicker{
	format?: string;
	startDate?: string;
	endDate?: string;
	todayBtn?: string;
	daysOfWeekDisabled?:number[];
	daysOfWeekHighlighted?:number[];
	autoclose?: boolean;
	language?: string;
	todayHighlight?: boolean;
	startView?:EDatePickerStartType;
}

export enum EDatePartType{
	day,month,year
}

/**
 * @classdesc Componente utilizado para a obtenção de Datas
 * 
 * <img src="../../doc_assets/calendar_input_exemple2.png">
 *
 * @example
 * 
 * import {DatePickerInput} from "mvcomponents/input";
 * 
 * let datePickerInput:DatePickerInput = new DatePickerInput({language: "es"})
 *     .setLabel("Date Picker Simples")
 *     .setMask("DD-MM-YYYY");
 * 
 * this.append(datePickerInput);
 * 
 * //linguas disponiveis: default "us", "pt-BR", "es"
 * 
 * @export
 * @extends {AInput}
 * @memberof module:mvcomponents/input
 */
export class DatePickerInput extends AInput<Date>{
	private format:string;

	constructor(options:IConfigDatePicker={}){
		super("text", "");
		this
			.addStyleName("DatePickerInput")
			.append(new InputAddon()
				.setIcon("glyphicon glyphicon-calendar"));
		this.setSize(3);

		var config:IConfigDatePicker = <IConfigDatePicker>Object.assign({
			todayBtn: "linked",
			autoclose: true,
			todayHighlight: true
		}, options);

		options.format = options.format || "DD[/]MM[/]YYYY";
		config.format = options.format.replace(/\[|\]/g,"").toLowerCase();
		
		this.format = options.format;
		this.getInput().datepicker(config);
	}

	public encode(value: Date): string {
		return value && moment(value).format(this.format);
	}

	public decode(value: string): Date {
		return value && moment(value, this.format).toDate();
	}

	/**
	 * Informa a mascara que será utilizada pelo input
	 * 
	 * @example
	 * 
	 * let datePickerInput:DatePickerInput = new DatePickerInput({language: "pt-BR"})
	 *     .setLabel("Date Picker Simples")
	 *     .setMask("DD-MM-YYYY");
	 * 
	 *  
	 * @param {string} mask Texto para indicar o formato da data. (Ex.: "DD-MM-YYYY","DD/MM/YYYY","YYYYMMDD")
	 * @returns {DatePickerInput}
	 */
	public setMask(mask: string): this {
		this.getInput().mask(mask);
		return this;
	}
}
